/* Grid & Grid content */
.page-header.navbar .menu-toggler {
  background-image: url("/images/sidebar-toggle-light.png"); }

.ui-grid {
  min-height: 380px; }
  .ui-grid .dropdown.open {
    position: fixed;
    z-index: 10; }

.tooltip {
  z-index: 999; }

input.ng-invalid.ng-touched {
  border-left: 5px solid red; }

input.ng-valid.ng-touched {
  border-left: 5px solid forestgreen; }

/* Material Design form input angular validation colors */
.form-group.form-md-line-input .form-control.ng-touched.ng-invalid {
  border-bottom-color: red; }

.form-group.form-md-line-input .form-control.ng-touched.ng-invalid.edited:not([readonly]) ~ label:after {
  background-color: red; }

.page-footer-inner {
  padding-bottom: 10px; }

.md-shadow-zero-light {
  box-shadow: 0 0 0 rgba(0, 0, 0, 0.1), 0 0 1px rgba(0, 0, 0, 0.18) !important; }

/* Modal fix for Metronic */
body[ng-controller] div.modal-backdrop {
  z-index: 10049; }

body[ng-controller] div.modal {
  z-index: 10050; }

/* even in RTL languages below types are LTR */
.input-ltr,
input[type="email"],
input[type="password"],
input[type="url"],
input[type="number"],
input[type="tel"] {
  direction: ltr; }

/* Customise Bootstrap Panel */
.panel {
  border-radius: 0;
  position: relative; }

.panel-heading {
  border-radius: 0; }

.panel-body {
  position: relative; }

.panel-default > .panel-heading {
  background-color: white; }

.panel-body > .form-group:last-child {
  margin-bottom: 0; }

.dropdown-menu {
  min-width: 60%;
  z-index: 10; }
  .dropdown-menu > li > a {
    text-align: center; }

.red-title {
  color: #e14147;
  font-weight: bold;
  font-size: 18px;
  line-height: 31px; }

.portlet-title.red-title {
  margin-bottom: 20px; }

.portlet.light .portlet-body {
  padding-top: 0;
  padding-bottom: 10px; }

ul.ng-scope {
  margin: 0;
  padding: 0; }

ul.list-inline {
  margin: 0px;
  list-style: none;
  width: 100%; }
  ul.list-inline li {
    line-height: 28px;
    font-size: 13px;
    width: 100%;
    padding: 0px; }
    ul.list-inline li span {
      padding: 0px 3px 0px 0px; }
    ul.list-inline li.sub-list {
      padding-left: 20px; }

body {
  font-size: 14px; }

.sweet-alert .icon,
.sweet-alert .icon.success .placeholder {
  -ms-border-radius: 50% !important;
  border-radius: 50% !important; }

#LanguageDropDown .btn {
  border: none;
  padding: 2px; }

#LanguageDropDown li.language-menu-item a {
  padding: 8px 5px; }

/* General */
.clickable-item {
  cursor: pointer; }

.text-bold {
  font-weight: bold; }

/* Portlet */
.portlet.light > .portlet-title .inputs.inputs-full-width {
  display: block;
  float: none; }

/* Tree */
.tree-item-icon-color {
  color: #dfba49; }

.page-sidebar .page-sidebar-menu > li > a > i.fa,
.page-sidebar-closed.page-sidebar-fixed .page-sidebar:hover .page-sidebar-menu > li > a > i.fa {
  margin-left: 1px;
  margin-right: 4px;
  top: 3px; }

/* Forms */
.form-group.form-md-line-input.no-hint {
  margin-bottom: 15px; }

.form-group.form-md-line-input.has-error .form-control {
  border-bottom-color: red; }

.form-group.form-md-line-input.has-error .help-block-info {
  display: none; }

/* jTable */
.jtable-main-container {
  overflow-x: auto; }
  .jtable-main-container .jtable .jtable-column-header {
    height: 32px; }
  .jtable-main-container .jtable .jtable-data-row td {
    height: 32px; }
  .jtable-main-container .jtable-bottom-panel select {
    color: black; }

/* DateRangePicker */
.daterangepicker_input .input-mini {
  width: 120px !important; }

/* Portlets */
.portlet.light > .portlet-title.portlet-title-filter {
  border: none;
  margin-bottom: 0; }

/* Buttons */
button i {
  margin-right: 5px; }

.btn + .btn {
  margin-left: 5px; }

/* External Login */
.social-icons li .openidconnect,
.social-login-icon.openidconnect {
  background: url(https://axa.azureedge.net/azure/images/icons/open-id-connect.png) no-repeat; }

.social-icons li .federation,
.social-login-icon.federation {
  background: url(https://axa.azureedge.net/azure/images/icons/ws-federation.png) no-repeat; }

/* fix for issue #185 */
.margin-bottom-0 {
  margin-bottom: 0 !important; }

/*style for uneditable-rows*/
.uneditable-row {
  color: #a0a0a0; }

/*multi tenant permission*/
.label-tenant {
  margin-right: 3px; }

.inline-checkbox {
  float: left;
  margin-right: 10px;
  text-transform: uppercase; }

/*scroll bar*/
.scrollbar::-webkit-scrollbar {
  width: 5px; }

.scrollbar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.3); }

.scrollbar::-webkit-scrollbar-thumb {
  background: gray;
  -webkit-box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.2); }

.scrollbar::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.2); }



